*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2"), url("../fonts/lato/lato-regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswald-regular.woff2"), url("../fonts/oswald/oswald-regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswald-medium.woff2"), url("../fonts/oswald/oswald-medium.woff");
  font-display: swap;
}
.page {
  height: 100%;
}

.container {
  width: 100%;
  max-width: 1280px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 768px) {
  .container--no-indent {
    padding: 0;
    margin: 0;
  }
}
@media (min-width: 1280px) {
  .container--no-indent-left {
    padding: 0 0 0 30px;
  }
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}

.page-body__main {
  overflow: hidden;
}

.page-body__main-header {
  margin: 0;
  padding: 26px 20px 41px 20px;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: #000000;
}
@media (max-width: 767px) {
  .page-body__main-header {
    border-bottom: 1px solid #ebebeb;
  }
}
@media (min-width: 768px) {
  .page-body__main-header {
    margin: 60px 0 163px 0;
    padding: 0;
    font-size: 60px;
    line-height: 60px;
    text-align: left;
  }
}
@media (min-width: 1280px) {
  .page-body__main-header {
    margin: 74px 0 165px 0;
  }
}

@media (max-width: 767px) {
  .page-body__main-container {
    margin: 0;
    padding: 0;
  }
}

@media (max-width: 767px) {
  .main-nav--no-js {
    margin: 0 -20px;
  }
}
@media (max-width: 767px) {
  .main-nav--no-js .main-nav__wrapper {
    padding: 0 20px 12px 20px;
  }
}
@media (max-width: 767px) {
  .main-nav--no-js .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--no-js .main-nav__list {
    position: static;
    display: block;
    width: 100%;
  }
}

.main-nav__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    padding: 0 0 11px 0;
  }
}

.main-nav__list {
  position: absolute;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .main-nav__list {
    display: none;
    left: 0;
    right: 0;
    z-index: 3;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    gap: 10px 34px;
    max-width: 500px;
    background-color: transparent;
  }
}
@media (min-width: 1280px) {
  .main-nav__list {
    max-width: 50%;
  }
}

@media (max-width: 767px) {
  .main-nav__item {
    border-top: 1px solid #e6e6e6;
  }
}

.main-nav__link {
  display: block;
  padding: 22px 20px 24px 20px;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
  transition: 0.3s ease;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link--current {
  pointer-events: none;
}
@media (min-width: 768px) {
  .main-nav__link--current {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1280px) {
  .main-nav__link--current-contrast {
    border-bottom: 2px solid #ffffff;
  }
}
@media (min-width: 1280px) {
  .main-nav__link--contrast {
    color: #ffffff;
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    padding: 8px 0;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (min-width: 1280px) {
  .main-nav__link {
    font-size: 20px;
    line-height: 30px;
  }
}

.main-nav__toggle {
  position: relative;
  width: 24px;
  height: 16px;
  cursor: pointer;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__toggle-line {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #000000;
  transition: 0.3s ease;
}
.main-nav__toggle-line:nth-child(1) {
  top: 0;
}
.main-nav__toggle-line:nth-child(2) {
  top: 8px;
}
.main-nav__toggle-line:nth-child(3) {
  top: 8px;
}
.main-nav__toggle-line:nth-child(4) {
  top: 16px;
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    display: block;
  }
}
.main-nav--opened .main-nav__toggle-line:nth-child(1) {
  transform: scaleX(0);
}
.main-nav--opened .main-nav__toggle-line:nth-child(2) {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle-line:nth-child(3) {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle-line:nth-child(4) {
  transform: scaleX(0);
}

